<template>
	<view class="container">
		<!-- 门店地图标记 -->
		<view class="page-body">
			<view class="page-section">
				<!-- 纬度                  经度 -->																		<!-- markers标识位置   标记点用于在地图上显示标记的位置-->
				<map id="myMap" style="width: 100%; height: 400rpx;" :latitude="latitude" :longitude="longitude" :markers="markers"></map>
			</view>
		</view>
		<!-- 店铺列表 -->
		<view class="content">
			<view class="store" v-for="(store, index) in storeData" :key="index" @tap="tapStore(store)">
				<view class="store-left">
					<view class="store-title">
						<view class="store-name">{{ store.name }}</view>
						<view class="store-status">营业中</view>
					</view>
					<view class="store-content">
						<view class="store-distance">距离您{{store.distance}}km</view>
						<text class="store-text">{{ store.street }}</text>
						<text class="store-text">营业时间：10：00-22:30</text>
					</view>
				</view>
				<view class="store-right">
					<view class="look">去看看</view>
					<view class="icon">
						<view class="round">
							<image src="../../static/images/store/store-tel.png"></image>
						</view>
						<view class="round">
							<image src="../../static/images/store/store-nav.png"></image>
						</view>
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import {
		mapMutations
	} from 'vuex';
	export default {
		data() {
			return {
				latitude: '',
				longitude: '',
				markers: [],
				storeData: []
			};
		},
		onLoad() {
			this.getStoreData();
		},
		methods: {
			...mapMutations(['SET_ORDERTYPE', 'SET_STORE']),
			getStoreData() {
				return new Promise((resolve, reject) => {
						uni.getLocation({
							// uni-app 只支持 gcj02 坐标
							type: 'gcj02',
							success(resLocation) {
								// 如果该坐标的经纬度存在,才解析数据 
								if (resLocation.latitude && resLocation.longitude) {
									resolve(resLocation);
								}
							}
						});
					})
					.then(local => {
						// 获取纬度
						let la1 = local.latitude;
						// 获取经度
						let lo1 = local.longitude;
						return uniCloud
						//请求stores函数
							.callFunction({
								name: 'stores'
							})
							.then(resStore => {
								let temp = [];
								let l = resStore.result.length;
								//获取所有的门店数据
								//计算当前位置与所有门店距离，存到数组中，再把距离排序，数组添加
								if (l >= 1) {
									for (let i = 0; i < 1; i++) {
										let element = resStore.result[i];
										//两点之间的距离
										let d = this.distance(element.latitude, element.longitude, la1, lo1);
										console.log("距离是:" + d)
										element.distance = d;
										//数组添加距离数据
										temp.push(element);
									}
									// 数组距离排序
									this.storeData = temp.sort(function(a, b) {
										return a.distance - b.distance;
									});
									return this.storeData;
								}
							});
					})
					.then(resMap => {
						console.log("获取到的信息是: ")
						console.log(resMap)
						console.log("获取到的信息是: ")
						this.latitude = resMap[0].latitude;
						this.longitude = resMap[0].longitude;
						let map = [];
						let len = resMap.length;
						if (len >= 1) {
							for (let i = 0; i < len; i++) {
								let maps = {};
								maps.id = i;
								maps.latitude = resMap[i].latitude;
								maps.longitude = resMap[i].longitude;
								maps.iconPath = '/static/images/logo.png';
								maps.width = 30;
								maps.height = 30;
								maps.callout = {
									content: resMap[i].name,
									display: 'ALWAYS'
								};
								map.push(maps);
							}
							// 把最近的门店位置赋予给标记点
							// 展示效果：标记点指向最近的门店
							this.markers = map;
							console.log(this.markers);
						}
					});
			},
			distance(la1, lo1, la2, lo2) {
				var La1 = (la1 * Math.PI) / 180.0;
				var La2 = (la2 * Math.PI) / 180.0;
				var La3 = La1 - La2;
				var Lb3 = (lo1 * Math.PI) / 180.0 - (lo2 * Math.PI) / 180.0;
				var s = 2 * Math.asin(Math.sqrt(Math.pow(
					Math.sin(La3 / 2), 2) + Math.cos(La1) * Math.cos(La2) * Math.pow(Math.sin(Lb3 / 2), 2)));
				s = s * 6378.137;
				s = Math.round(s * 10000) / 10000;
				s = s.toFixed(1);
				console.log(s)
				return s;
			},
			tapStore(store) {
				this.SET_ORDERTYPE('takein');
				this.SET_STORE(store);
				uni.switchTab({
					url: '../menu/menu'
				});
			}
		}
	};
</script>

<style lang="scss" scoped>
	.page-map {
		width: 100%;
		height: 400rpx;
	}

	.content {
		width: 100%;
		height: 100%;
	}

	.store {
		display: flex;
		flex-wrap: nowrap;
		margin: 10rpx 20rpx;
		padding: 20rpx;
		background-color: #ffffff;
		box-shadow: $box-shadow;
	}

	.store-left {
		width: 70%;
		display: flex;
		flex-direction: column;

		.store-title {
			display: flex;
			flex-wrap: nowrap;

			.store-name {
				color: $text-color-black;
				font-size: $font-size-lg;
				font-weight: bold;
				margin-right: 50rpx;
				white-space: nowrap;
				text-overflow: ellipsis;
				overflow: hidden;
			}

			.store-status {
				color: $color-primary;
				font-size: $font-size-sm;
				padding: 0 5rpx;
				border: 1rpx solid $color-primary;
				white-space: nowrap;
			}
		}

		.store-content {
			margin-top: 60rpx;
			font-size: $font-size-sm;
			display: flex;
			flex-direction: column;

			.store-text {
				color: $text-color-assist;
				white-space: nowrap;
				text-overflow: ellipsis;
				overflow: hidden;
			}
		}
	}

	.store-right {
		width: 30%;
		display: flex;
		flex-direction: column;
		align-items: flex-end;

		.look {
			font-size: $font-size-sm;
			color: $color-primary;
		}

		.icon {
			display: flex;
			flex-wrap: nowrap;
			align-items: center;
			margin-top: 60rpx;
			justify-content: flex-end;

			.round {
				border-radius: 50%;
				width: 60rpx;
				height: 60rpx;
				margin: 10rpx;
				background-color: #cad43f;

				image {
					display: block;
					width: 40rpx;
					height: 40rpx;
					margin: 10rpx;
					align-items: center;
				}
			}
		}
	}
</style>
